<template>
  <label class="Label">
    <input
      type="radio"
      class="Radio"
      :class="radioClasses"
      :name="inputName"
    />
    <span class="Name">{{name}}</span>
  </label>
</template>

<script>
export default {
  props: ['name', 'radioClasses', 'inputName'],
};
</script>

<style lang="stylus" scoped>

.Label
  display flex
  align-items center
  cursor pointer

.Name
  margin-left 5px
</style>



