<template>
  <div class="SearchResult">
    <div class="SearchResult-searchOptions">
      <div class="SearchResult-options">
        <SearchResultOptions />
      </div>

      <div class="SearchResult-data">
        <SearchResultItem
          v-for="(item, index) in result"
          v-bind:key="index"
          :item="item"
        />
      </div>
    </div>
  </div>
</template>

<script>
import SearchResultOptions from '@/features/Result/ResultOptions';
import SearchResultItem from '@/features/Result/Item';
import API from '@/api/auth';

export default {
  mounted() {
    this.getResult();
  },
  components: {
    SearchResultOptions,
    SearchResultItem,
    API,
  },
  data() {
    return {
      result: [],
    };
  },
  methods: {
    getResult() {
      this.result = API.search();
    },
  },
};
</script>

<style scoped>
.SearchResult {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: 0 auto;
}
.SearchResult-separator {
  width: 100%;
	height: 3px;
	border-style: solid;
	border-width: 1px;
	border-image-source: radial-gradient(circle at 50% 0, #ffffff, rgba(255, 255, 255, 0.4));
	border-image-slice: 1;
}
.SearchResult-searchOptions {
  margin-top: 36px;
}
.SearchResult-options {
  /* width: 50%; */
}
</style>




