<template>
  <div class="SearchResultOptions" :class="{'isScrolled': scrolled}">
    <div class="SearchResultOptions-layout">
      <div class="SearchResultOptions-sticky">
        <Layout>
          <LogoComponent class="logo-result"></LogoComponent>
          <div
            class="SearchResultOptions-item SearchResultOptions-item--searchBox"
          >
            <Layout>
              <SearchBox
                :value="initialKeyword"
                type="result"
              />
              <DateFilter/>
              <DatePicker/>
            </Layout>
            <SearchVariants
              class="SearchResultOptions-item SearchResultOptions-item--checkboxes"
            />
          </div>
        </Layout>
      </div>
      <SortBy
        class="SearchResultOptions-item SearchResultOptions-item--radios"
      />
    </div>
  </div>
</template>

<script>
import LogoComponent from '@/features/LogoComponent';
import DateFilter from '@/features/DateFilter';
import DatePicker from '@/features/DatePicker';
import Layout from './Layout';
import SearchVariants from './Variants';
import SortBy from './SortBy';
import SearchBox from '../SearchBox';

export default {
  components: {
    SearchBox,
    DateFilter,
    DatePicker,
    Layout,
    LogoComponent,
    SearchVariants,
    SortBy,
  },
  computed: {
    initialKeyword() {
      return this.$route.query.keyword;
    },
  },
  data() {
    return {
      scrolled: false,
      nodes: [],
    };
  },
  mounted() {
    document.addEventListener('scroll', () => {
      this.scrolled = window.pageYOffset > 160;
    }, false);
  },
  methods: {
  },
};
</script>

<style lang="less" scroped>
.SearchResultOptions {
  position: relative;
  width: 100%;
  left: 0;
  top: 0;
  padding: 0;
  box-shadow: none;
  background-color: transparent;
  &.isScrolled  {
    .logo-result {
      display: block;
    }
    .SearchResultOptions-sticky {
      position: fixed;
      top: 0;
      left: 0;
      box-shadow: 0px 2px 5px 0px rgba(214,214,214,1);
      padding: 30px 0 31px;
      background-color: #eaeaea;
      width: 100%;
      padding-left: 10%;
      padding-right: 10%;
      background-image: radial-gradient(circle at 57% 20%, rgba(255, 255, 255, 0.52), #eaeaea);
    }
    .SearchResultOptions-item--logo {
      display: block;
      flex: 0 0 12%;
    }
    .SearchResultOptions-item--searchBox {
      flex: 0 0 46%;
    }
    .SearchResultOptions-item--searchOption {
      flex: 0 0 36%;
      .container {
        position: relative;
      }
    }
    .SearchResultOptions-item--checkboxes {
      /*margin-left: 12%;
      margin-top: -18px;*/
    }
  }
}

.SearchResultOptions-layout {
  display: flex;
  margin: 0 auto;
  width: auto;
  flex-flow: row wrap;
}
.SearchResultOptions-sticky {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
}
.SearchResultOptions-item {
  flex: 0 0 50%;
}
.SearchResultOptions-label {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.SearchResultOptions-label span {
  margin-left: 5px;
}
.SearchResultOptions-item--checkboxes {
  flex: 0 0 100%;
  display: flex;
  margin-left: 0;
}
.SearchResultOptions-item--radios {
  flex: 0 0 100%;
  display: flex;
  margin-top: 46px;
  display: flex;
  align-items: center;
}
.SearchResultOptions-sortByLabel {
  font-size: 14px;
  margin-right: 16px;
}
.SearchResultOptions-item--logo {
  display: none;
}
.SearchResultOptions-item--searchBox {
  flex: 0 0 72%;
}
.SearchResultOptions-item--searchOption {
  flex: 1 0 23%;
  margin-left: 5%;
}
.SearchResultOptions-optionBox {
  // margin-left: 60px;
}
.SearchResultOptions-checkboxWrapper {
  flex: 0.04 0 auto;
}
.logo-result {
  display: none;
}
</style>



