<template>
  <div>
    <span class="SearchResultOptions-sortByLabel">Sort By:</span>
    <div
      class="SearchResultOptions-checkboxWrapper"
      v-for="(checked, name) in dataToRender"
    >
        <div class="BlockShouldRename-cell second">
          <Radio v-if="isInput('radio')"
            :name="name"
            radioClasses="MyRadio"
            inputName="myRadio"
          />
        </div>
    </div>
  </div>
</template>

<script>
import Radio from '@/components/Radio';
import InputGroup from '@/helpers/InputGroup';
import { mapGetters } from 'vuex';

export default {
  components: {
    Radio,
  },
  extends: InputGroup,
  props: {
    data: {
      default() {
        return this.dataToRender;
      },
    },
    type: {
      default() {
        return 'radio';
      },
    },
    onChange: {
      default() {
        return (data) => {
          this.$store.dispatch('setSortBy', data);
        };
      },
    },
  },
  computed: {
    ...mapGetters({
      dataToRender: 'sortByDataToRender',
    }),
  },
};
</script>

<style lang="stylus">
.MyRadio {
  -webkit-appearance: none;
  -moz-appearance: none;
}

.MyRadio + * {
  padding: 0 8px;
  cursor: pointer;
  border-bottom: 2px dotted rgba(192, 192, 192, .3);
}

.MyRadio:checked + * {
  background-color: rgba(192, 192, 192, .3);
  border-color: transparent;
}
</style>


